<template>
    <div class="container mt-3">
        <h1>Ventas</h1>
        <div>
            <h3 v-if="juegos.length > 0">Juegos disponibles: {{juegos.length}}</h3>
        </div>

        <div class="container">
            <table class="table table-stripped">
                <thead>
                    <tr>
                        <th>Nombre</th>
                        <th>Stock</th>
                        <th>Precio</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(juego, i) in juegos" :key="i">
                        <td>{{juego.nombre}}</td>
                        <td>{{juego.stock}}</td>
                        <td>{{juego.precio}}</td>
                        <td>
                            <button  class="btn btn-success" type="button" @click="vender(i), agregarVenta(juego), totalVentas(juego)">comprar</button>
                        </td>
                    </tr>
                </tbody>
            </table>

        </div>
    </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'



export default {
    computed:{
        ...mapState(['juegosConStock', 'ventas']),
        juegos(){
            return this.juegosConStock
        }

    },


    methods:{
        ...mapActions(['agregarVenta', 'totalVentas', 'actualizarStock', 'venderProducto']),
        nuevoStock(){
            this.actualizarStock()
        },
        vender(j){
            this.venderProducto(j)
        },
    }

}
</script>

